<template>
  <Layout class="professional-page">
    <Row class="tool-bar">
      <Button type="primary" @click.native="$router.push({ name: 'ProfessionalDetail', params: { type: 'new', data: 0 } })">添加专业</Button>
    </Row>
    <Row class="professional-list">
      <Table class="professional-table" :columns="columns" :data="professionalList"></Table>
    </Row>
    <Row class="pagination">
      <Page :total="paginationData.total" @on-change="changePage" @on-page-size-change="changePageSize" show-elevator show-total show-sizer></Page>
    </Row>
  </Layout>
</template>

<script>
import { getProfessionalList } from '@/api/professional'

import paginationMixins from './mixins/paginationMixins'

export default {
  name: 'professional',
  mixins: [ paginationMixins ],
  data () {
    return {
      columns: [
        {
          type: 'index',
          width: 60
        },
        // {
        //   title: 'ID',
        //   key: 'id',
        //   width: 60
        // },
        {
          title: '专业名称',
          render: (h, { row, column, index }) => {
            let indent = ''
            for (let i = 0; i < +row.level; i++) {
              indent = indent + '------'
            }
            return h(
              'span',
              indent + row.name
            )
          }
        },
        {
          title: '别名',
          key: 'alias'
        },
        {
          title: '排序',
          key: 'sort'
        },
        {
          title: '操作',
          render: (h, { row, column, index }) => {
            return h(
              'a',
              {
                domProps: {
                  href: 'javascript: void(0)'
                },
                props: {
                  size: 'small',
                  type: 'text'
                },
                on: {
                  click: () => {
                    console.log(row)
                    this.editProfessional(JSON.stringify(row))
                  }
                }
              },
              '编辑'
            )
          }
        }
      ],
      professionalList: [],
      originData: []
    }
  },
  created () {
    this.getProfessionalList()
  },
  methods: {
    getProfessionalList () {
      getProfessionalList({}).then(resp => {
        if (resp.response.success) {
          this.originData = resp.data
          this.paginationData.total = this.originData.length
          this.professionalList = this.getPageData(this.originData, this.paginationData)
        } else {
          this.$Message.error(resp.response.msg)
        }
      }).catch(err => {
        console.error(err)
      })
    },
    editProfessional (data) {
      this.$router.push({ name: 'ProfessionalDetail', params: { type: 'edit', data } })
    },
    changePage (page) {
      this.paginationData.current = page
      this.professionalList = this.getPageData(this.originData, this.paginationData)
    },
    changePageSize (size) {
      this.paginationData.pageSize = size
      this.professionalList = this.getPageData(this.originData, this.paginationData)
    }
  }
}
</script>

