export default {
  data () {
    return {
      paginationData: {
        total: 10,
        pageSize: 10,
        current: 1
      }
    }
  },
  methods: {
    getPageData (data, paginationData) {
      return data.slice(paginationData.pageSize * (paginationData.current - 1), paginationData.pageSize * (paginationData.current))
    }
  }
}
